@use '../utils/variables';

/*=====================
  Title css
==========================*/
.title1 {
  text-align: center;

  h4 {
    color: var(--theme-color);
    padding-bottom: 10px;
    margin-bottom: 0;
    margin-top: -3px;

    @media (max-width: 576px) {
      margin-top: 0;
    }
  }

  .title-inner1 {
    padding-bottom: 15px;
    margin-bottom: 30px;
    position: relative;

    &:after {
      position: absolute;
      height: 5px;
      width: 70px;
      background-color: var(--theme-color);
      content: '';
      left: 0;
      right: 0;
      margin: 0 auto;
      bottom: 0;
    }
  }

  &.title-gradient {
    .title-inner1 {
      &:after {
        background: var(--theme-color);
        background: linear-gradient(180deg, var(--theme-color) 0%, var(--theme-color2) 99%);
        background-color: var(--theme-color);
      }
    }
  }

  &.title5 {
    .title-inner1 {
      padding-bottom: 0;
      margin-bottom: 0;

      &:after {
        display: none;
      }
    }

    hr[role='tournament6'] {
      border: 0px solid;
      height: 1px;
      background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0),
        var(--theme-color),
        rgba(0, 0, 0, 0)
      );
      display: block;
      width: 25%;
      margin: 15px auto 30px auto;
    }

    hr[role='tournament6']::before {
      position: absolute;
      background-color: #efefef;
      border: 1px solid;
      border-color: var(--theme-color);
      padding: 5px;
      -webkit-transform: rotate(45deg);
      transform: rotate(45deg);
      left: 50%;
      margin: -6px 0px 0px 0;
      content: '';
    }
  }

  &.wo-border {
    .title-inner1 {
      padding-bottom: 0;
      margin-top: -7px;
      margin-bottom: 25px;

      &:after {
        display: none;
      }
    }
  }

  [class*='skeleton-text-'] {
    display: none;
  }
}

.title2 {
  text-align: center;
  position: relative;
  z-index: 1;

  h4 {
    color: var(--theme-color);
    padding-bottom: 10px;
    margin-bottom: 0;
    text-transform: uppercase;
    margin-top: -3px;
  }

  .title-inner2 {
    margin-bottom: 50px;
    position: relative;
    display: inline-block;
    padding: 0 25px;

    &:after {
      position: absolute;
      height: 5px;
      width: 100%;
      background-color: var(--theme-color);
      opacity: 0.4;
      content: '';
      left: 0;
      right: 0;
      margin: 0 auto;
      bottom: 3px;
      z-index: -1;
    }
  }
}

.title3 {
  text-align: center;

  h4 {
    color: var(--theme-color);
    padding-bottom: 10px;
    margin-bottom: 5px;
    margin-top: -3px;

    &:empty {
      display: none;
    }
  }

  .title-inner3 {
    margin-bottom: 20px;
    position: relative;
    margin-top: -5px;
  }

  .line {
    position: relative;
    height: 5px;
    width: 45px;
    background-color: var(--theme-color);
    content: '';
    left: 0;
    right: 0;
    margin: 0 auto;
    bottom: 0;
    margin-bottom: 50px;

    &:after,
    &:before {
      position: absolute;
      height: 5px;
      width: 10px;
      content: '';
      background-color: var(--theme-color);
    }

    &:after {
      right: 50px;
    }

    &:before {
      left: 50px;
    }
  }
}

.title4 {
  text-align: center;

  .title-inner4 {
    padding-bottom: 25px;
    margin-bottom: 0;
    position: relative;
    margin-top: calc(0px + (-5 - 0) * ((100vw - 320px) / (1920 - 320)));
  }

  .line {
    position: relative;
    height: 1px;
    width: 180px;
    background-color: var(--theme-color);
    margin: 0 auto;
    margin-bottom: 55px;
    bottom: 0;
    top: 0;

    &:after,
    &:before {
      position: absolute;
      height: 10px;
      width: 10px;
      content: '';
      background-color: var(--theme-color);
      top: -4px;
    }

    &:after {
      left: 78px;
    }

    &:before {
      left: 64px;
    }

    span {
      &:after,
      &:before {
        position: absolute;
        height: 10px;
        width: 10px;
        content: '';
        background-color: var(--theme-color);
        top: -4px;
      }

      &:after {
        right: 78px;
      }

      &:before {
        right: 64px;
      }
    }
  }
}

.title6 {
  text-align: center;

  h2 {
    margin-top: -5px;
    margin-bottom: 20px;
  }
}

.title8 {
  margin-bottom: calc(12px + (22 - 12) * ((100vw - 320px) / (1920 - 320)));

  h2 {
    font-size: calc(16px + (24 - 16) * ((100vw - 320px) / (1920 - 320)));
    margin-bottom: 0;
    text-transform: capitalize;
  }

  p {
    font-size: 16px;
    margin-bottom: 0;
    margin-top: calc(6px + (10 - 6) * ((100vw - 320px) / (1920 - 320)));
    text-transform: capitalize;
  }
}

.title-borderless {
  margin-bottom: 30px;
  text-align: center;
  margin-top: calc(-3px + (-5 - -3) * ((100vw - 320px) / (1920 - 320)));
}

.about-text {
  p {
    line-height: 28px;
    letter-spacing: 0.06em;
    text-align: center;
    margin-bottom: 50px;
  }
}

.theme-tab {
  .title-inline {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    border-bottom: 1px solid #dddddd;

    h3 {
      text-transform: capitalize;
      margin-bottom: 0;
      color: black;
      font-weight: 600;
    }

    .tab-title {
      margin-bottom: 0;
      margin-left: auto;
      margin-top: 0;

      li {
        padding-left: 0;
        padding-right: 0;
        padding-bottom: 10px;
        margin: 0 15px;
      }

      .current {
        border-bottom: 2px solid var(--theme-color);
      }
    }
  }
}

.title-basic {
  display: flex;
  align-items: center;
  margin-bottom: calc(16px + (22 - 16) * ((100vw - 320px) / (1920 - 320)));

  .title {
    font-size: calc(16px + (24 - 16) * ((100vw - 320px) / (1920 - 320)));
    margin-bottom: 0;

    i {
      font-weight: normal;
    }
  }

  .timer {
    margin-top: 0;
    margin-left: 15px;
    background-color: #f5f5f5;

    p {
      color: #000000;
    }

    span {
      .timer-cal {
        display: none;
      }
    }
  }
}

.title-gradient {
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 400% 400%;
  -webkit-animation: gradient 15s ease infinite;
  animation: gradient 15s ease infinite;
}

.title7 {
  margin-bottom: calc(14px + (26 - 14) * ((100vw - 320px) / (1920 - 320)));
  text-align: center;
  width: 26%;
  margin-inline: auto;

  @media (max-width: 991px) {
    width: 45%;
  }

  @media (max-width: 575px) {
    width: 55%;
  }

  @media (max-width: 400px) {
    width: 100%;
  }

  h4 {
    font-size: calc(20px + (35 - 20) * ((100vw - 320px) / (1920 - 320)));
    color: variables.$white;
    font-weight: 600;
    margin: 0;
    padding: 0;
    line-height: 1.4;
    margin-top: -12px;
  }
}
