@use '../utils/variables';

/**=====================
  Country flag css
==========================**/
$color_1: #999;
$background-color_2: #efefe7;
$background-color_3: transparent;
$background-color_4: #dbdbdb;

.intl-tel-input {
  position: relative;
  display: block;

  * {
    box-sizing: border-box;
    -moz-box-sizing: border-box;
  }

  .hide {
    display: none;
  }

  .v-hide {
    visibility: hidden;
  } 

  input {
    position: relative;
    z-index: 0;
    margin-top: 0 !important;
    margin-bottom: 0 !important;
    padding-right: 36px;
    margin-right: 0;

    [type='text'],
    [type='tel'] {
      position: relative;
      z-index: 0;
      margin-top: 0 !important;
      margin-bottom: 0 !important;
      padding-right: 36px;
      margin-right: 0;
    }
  }

  .flag-container {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    padding: 1px;
  }

  .selected-flag {
    z-index: 1;
    position: relative;
    width: 36px;
    height: 100%;
    padding: 0 8px 0 8px;

    .iti-flag {
      position: absolute;
      top: 0;
      bottom: 0;
      margin: auto;
    }

    .iti-arrow {
      position: relative;
      top: 0;
      margin-top: -2px;
      right: 0;
      width: 0;
      height: 0;
      border-left: 3px solid transparent;
      border-right: 3px solid transparent;
      border-top: 4px solid #555;
    }

    .iti-arrow.up {
      border-top: none;
      border-bottom: 4px solid #555;
    }
  }

  .flag-box {
    display: inline-block;
    width: 20px;
    vertical-align: middle;
    margin-right: 6px;
  }

  .divider {
    padding-bottom: 5px;
    margin-bottom: 5px;
    border-bottom: 1px solid #ccc;
  }

  .country {
    padding: 0;
    display: flex;
    align-items: center;
    width: 100%;

    .dial-code {
      color: $color_1;
    }
  }

  .country.highlight {
    background-color: $background-color_2;
  }

  .country-name {
    vertical-align: middle;
    margin-right: 6px;
  }

  .dial-code {
    vertical-align: middle;
  }

  .country-listing {
    position: absolute;
    z-index: 2;
    list-style: none;
    text-align: left;
    padding: 0;
    margin: 0 0 0 -1px;
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
    background-color: variables.$white;
    border: 1px solid #ccc;
    white-space: nowrap;
    max-height: 200px;
    overflow-y: scroll;
  }

  .country-listing.dropup {
    bottom: 100%;
    margin-bottom: -1px;
  }
}

.intl-tel-input.allow-dropdown {
  input {
    padding-right: 6px;
    padding-left: 52px;
    margin-left: 0;
  }

  input[type='text'] {
    padding-right: 6px;
    padding-left: 52px;
    margin-left: 0;
  }

  input[type='tel'] {
    padding-right: 6px;
    padding-left: 52px;
    margin-left: 0;
  }

  .flag-container {
    right: auto;
    left: 0;

    &:hover {
      cursor: pointer;

      .selected-flag {
        background-color: $background-color_2;
      }
    }
  }

  .selected-flag {
    width: 46px;
  }

  input[disabled] {
    + {
      .flag-container {
        &:hover {
          cursor: default;

          .selected-flag {
            background-color: $background-color_3;
          }
        }
      }
    }
  }

  input[readonly] {
    + {
      .flag-container {
        &:hover {
          cursor: default;

          .selected-flag {
            background-color: $background-color_3;
          }
        }
      }
    }
  }
}

.intl-tel-input.separate-dial-code {
  input {
    padding-right: 6px;
    padding-left: 52px;
    margin-left: 0;
  }

  input[type='text'],
  input[type='number'] {
    padding-right: 6px;
    padding-left: 52px;
    margin-left: 0;

    &.flag-input {
      padding: 0 0 0 30px !important;
      box-shadow: none !important;
      border: 0 !important;
      background-color: transparent !important;
      width: 70px !important;
    }
  }

  input[type='tel'] {
    padding-right: 6px;
    padding-left: 112px !important;
    margin-left: 0;
  }

  .flag-container {
    right: auto;
    left: 0;
  }

  .selected-flag {
    width: max-content;
    background-color: $background-color_2;
    display: flex;
    align-items: center;
    gap: 10px;
  }

  .selected-dial-code {
    display: table-cell;
    vertical-align: middle;
    padding-left: 28px;
  }
}

