.intl-tel-input.iti-container {
  position: absolute;
  top: -1000px;
  left: -1000px;
  z-index: 1060;
  padding: 1px;

  &:hover {
    cursor: pointer;
  }
}

.iti-mobile {
  .intl-tel-input.iti-container {
    top: 30px;
    bottom: 30px;
    left: 30px;
    right: 30px;
    position: fixed;
  }

  .intl-tel-input {
    .country-list {
      max-height: 100%;
      width: 100%;

      .country {
        padding: 10px 10px;
        line-height: 1.5em;
      }
    }
  }
}

