@use '../utils/variables';

/**=====================
    Typography css
==========================**/
body {
  font-family: 'Montserrat', sans-serif;
  position: relative;
  background: variables.$white;
  font-size: 14px;

  &.christmas {
    section {
      overflow: hidden;
    }
  }

  &.mulish-font {
    font-family: 'Mulish', sans-serif;
  }

  &.single-product {
    background-color: rgba(10, 10, 22, 1);
  }
}

h1 {
  font-size: 60px;
  color: variables.$dark-font;
  font-weight: 700;
  text-transform: uppercase;

  span {
    font-size: 107px;
    font-weight: 700;
    color: var(--theme-color);
  }
}

h2 {
  font-size: 36px;
  color: variables.$dark-font;
  text-transform: uppercase;
  font-weight: 700;
  line-height: 1;
  letter-spacing: 0.02em;
}

h3 {
  font-size: 24px;
  font-weight: 400;
  color: variables.$grey;
  letter-spacing: 0.03em;
}

h4 {
  font-size: 18px;
  text-transform: capitalize;
  font-weight: 400;
  letter-spacing: 0.03em;
  line-height: 1;
}

h5 {
  font-size: 16px;
  font-weight: 400;
  color: variables.$dark-font;
  line-height: 24px;
}

h6 {
  font-size: 14px;
  font-weight: 400;
  color: variables.$grey;
  line-height: 24px;
}

ul {
  padding-left: 0;
  margin-bottom: 0;
}

li {
  display: inline-block;
}

p {
  font-size: 14px;
  color: variables.$grey;
  line-height: 20px;
}

a {
  transition: 0.5s ease;
  text-decoration: none;

  &:hover {
    text-decoration: none;
    transition: 0.5s ease;
  }

  &:focus {
    outline: none;
  }
}

button {
  &:focus {
    outline: none;
  }
}

.btn-close {
  &:focus {
    box-shadow: none;
  }
}

label {
  margin-bottom: 0.5rem;
}

:focus {
  outline: none;
}

.form-control {
  &:focus {
    box-shadow: none;
  }
}

.form-select {
  &:focus {
    box-shadow: none;
    border-color: #ced4da;
  }
}

.font-cormorant {
  font-family: variables.$font_3, variables.$font-serif;
}

.font-fraunces {
  font-family: 'Fraunces', serif;
}

.font-courgette {
  font-family: 'Courgette', cursive;
  text-transform: capitalize !important;
}

.text-theme {
  color: var(--theme-color) !important;
}

.form-check-input {
  &:checked {
    background-color: var(--theme-color);
    border-color: var(--theme-color);
  }
}
